<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      rect: null,
      time: "2019-6-20",
      title: "操作文本",
      items: [
        {
          title: "select事件选中文本",
          target: "#select"
        },
        { divider: true },
        {
          title: "range对象",
          target: "#range"
        },
        { divider: true },
        {
          title: "selection对象",
          target: "#selection"
        },
      ],
      pres: 
[
`
    const input = document.querySelector('textarea');
    const res = document.querySelector("#result");
    input.onselect = e => {
        res.removeChild(res.lastChild);
        res.appendChild(
            document.createTextNode(
                event.target.value.substring(
                    event.target.selectionStart,    // 选中文本开始位置
                    event.target.selectionEnd       // 选中文本结束位置
                )
            )
        );
    }

    // 使用activeElement获取焦点元素，一样可以实现相同的效果
    // document.onmouseup = () => {
    //     const focus = document.activeElement;
    //     console.log(focus.value.substring(focus.selectionStart, focus.selectionEnd));
    // };
`,
`
    <div>
        <textarea cols="30" rows="10">我在好几篇小说中都提到过一座废弃的古园，实际就是地坛。许多年前旅游业还没有开始，园子荒芜冷落得如同一片野地，很少被人记起。</textarea>
        <div id="result">
            <p>选中文本如下：</p>
        </div>
    </div>
`,
`
    textarea{
        float: left;
    }
    #result{
        float: left;
        width: 200px;
        height: 200px;
        background-color: aliceblue;
        margin-left: 15px;
        font-size: 12px;
        color: darkgray;
        padding: 10px;
        box-sizing: border-box;
        overflow: auto;
    }
`,
`
    const range = new Range();
    const p = document.querySelector('p');
    range.setStart(p.firstElementChild.firstChild, 2);  // 设置开始点，传入的第二个参数表示开始点在指定元素中的偏移量
                                                        // 如果第一个参数是文本节点，那么偏移量表示偏移的字符个数
                                                        // 如果第一个参数是元素节点，那么偏移量表示偏移的元素个数

    range.setEnd(p.lastElementChild.firstChild, 2);     // 设置结束点
    console.log(range.toString());                      // 获取选中的文本    
`,
`
    // 获取selection对象
    let selection = document.getSelection(); 
    const box = document.getElementById('box');
    
    document.onselectstart = function() {
        // 在每次开始选文本的时候触发，比onselectionchange触发早
    };
    document.onselectionchange = function() {
        // 当用户选择文本时候不断触发
        selection = document.getSelection();
    };
    box.onmouseup = () => {
        // 获取选中文本
        console.log(selection.toString()); 

        // 执行复制命令，文本复制到剪贴板，此时使用 ctrl + v 键得到选中的文本
        document.execCommand('copy'); 
    };

    // selection.selectAllChildren(box); // 传入一个元素表示全选元素的全部文本
`,
`
    // 复制事件
    box.addEventListener('copy', (event) => {
        const selection = document.getSelection();

        // 修改复制的数据
        event.clipboardData.setData('text/plain', "----" + selection.toString() + "----");
        event.preventDefault();
    });

    // 粘贴事件paste 和 剪切事件cut 使用方式和上面雷同不再赘述
    document.addEventListener('paste', (e) => {
        console.log(e);
    });
`
]
    };
  },
    mounted() {
        const input = document.querySelector('textarea');
        const res = document.querySelector("#result");
        input.onselect = e => {
            res.removeChild(res.lastChild);
            res.appendChild(
                document.createTextNode(
                    event.target.value.substring(
                        event.target.selectionStart,
                        event.target.selectionEnd
                    )
                )
            );
        }
    },
    methods: {
    },
    destroyed() {

    },
};
</script>

